'use strict';

/**
* Compute the variance of a single-precision floating-point strided array using a two-pass algorithm.
*
* @module @stdlib/stats-base-svariancepn
*
* @example
* import Float32Array from '@stdlib/array-float32' ;
* import svariancepn from '@stdlib/stats-base-svariancepn' ;
*
* var x = new Float32Array( [ 1.0, -2.0, 2.0 ] );
* var N = x.length;
*
* var v = svariancepn( N, 1, x, 1 );
* // returns ~4.3333
*
* @example
* import Float32Array from '@stdlib/array-float32' ;
* import floor from '@stdlib/math-base-special-floor' ;
* import svariancepn from '@stdlib/stats-base-svariancepn' ;
*
* var x = new Float32Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
* var N = floor( x.length / 2 );
*
* var v = svariancepn.ndarray( N, 1, x, 2, 1 );
* // returns 6.25
*/

// MODULES //

import { join as join } from 'path' ;
import tryRequire from '@stdlib/utils-try-require' ;
import main from './main.js' ;


// MAIN //

var svariancepn;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( tmp instanceof Error ) {
	svariancepn = main;
} else {
	svariancepn = tmp;
}


// EXPORTS //

export default svariancepn;

// exports: { "ndarray": "svariancepn.ndarray" }
